
.details {
  padding: 10px 15px;
  color: #fff;
  font-size: 14px;
}
.details .row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 5px 0;
}
.details .row .cell {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  line-height: 16px;
}
.details .row .cell.left {
  padding-right: 15px;
  text-align: right;
}
.details .row .cell.right {
  text-align: left;
}



