
.future {
  box-sizing: border-box;
  padding: 5px 15px;
}
.future .item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 5px 0;
  color: #fff;
  font-size: 14px;
}
.future .item .day {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  line-height: 25px;
  vertical-align: middle;
}
.future .item .icon {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  font-size: 25px;
  text-align: center;
}
.future .item .tmp {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  line-height: 25px;
  text-align: right;
}
.future .item .tmp .min {
  margin-right: 5px;
}
.future .item .tmp .max {
  display: inline-block;
  width: 20px;
}



