
.list {
  position: relative;
  width: 100%;
  height: 100%;
  color: #000;
  box-sizing: border-box;
  padding-top: 45px;
  padding-bottom: 45px;
}
.list .search {
  box-sizing: border-box;
  height: 45px;
  margin-top: -45px;
  box-shadow: 0 1px 5px #ccc;
  padding: 5px 15px;
  border-bottom: solid 1px #ccc;
  background: #e6e6e6;
}
.list .search .input {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  text-align: center;
  border-radius: 5px;
  border: solid 1px #ccc;
}
.list .list-wrapper {
  height: 100%;
  overflow: hidden;
}
.list .list-wrapper .item-list .title {
  font-size: 16px;
  font-weight: 700;
  padding: 5px 15px;
  background: #e6e6e6;
}
.list .list-wrapper .item-list ul {
  padding: 0 15px;
}
.list .list-wrapper .item-list ul .item {
  padding: 10px 0px;
  border-bottom: solid 1px #ccc;
}
.list .list-wrapper .item-list ul .item:last-child {
  border: none;
}
.list .cancel {
  height: 45px;
  line-height: 45px;
  text-align: center;
  background: #e6e6e6;
  border-top: solid 1px #ccc;
}
.list .translate-enter-active {
  transition: all .3s ease;
}
.list .translate-leave-active {
  transition: all .3s ease;
}
.list .translate-enter,
.list .translate-leave-active {
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
  opacity: 0;
}



