
.header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 250px;
  color: #fff;
}
.header .content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
}
.header .content .city {
  font-size: 20px;
  line-height: 28px;
  font-weight: 700;
}
.header .content .weather {
  font-size: 12px;
}
.header .content .temper {
  position: relative;
  display: inline-block;
  font-size: 60px;
  line-height: 80px;
}
.header .content .temper .dot {
  position: absolute;
  display: block;
  top: 0;
  right: -15px;
  font-size: 40px;
}



