
.slide-enter-active {
  transition: all .3s ease;
}
.slide-leave-active {
  transition: all 0.3s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-enter,
.slide-leave-active {
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
  opacity: 0;
}



