
.footer {
  position: fixed;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 100%;
  height: 45px;
  left: 0;
  bottom: 0;
  background: #1a375f;
}
.footer::before {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  border: solid 1px rgba(255, 255, 255, 0.3);
  content: '';
  display: block;
  box-sizing: border-box;
}
@media (min-device-pixel-ratio: 1.5), (-webkit-min-device-pixel-ratio: 1.5) {
.footer::before {
    transform: scaleY(0.7);
    -webkit-transform: scaleY(0.7);
}
}
@media (min-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 2) {
.footer::before {
    transform: scaleY(0.5);
    -webkit-transform: scaleY(0.5);
}
}
.footer span {
  display: inline-block;
  box-sizing: border-box;
  width: 35px;
  height: 35px;
  color: #fff;
  font-size: 35px;
}
.footer span.github {
  margin-left: 15px;
}
.footer span.change-city {
  position: absolute;
  right: 15px;
}
.footer span a {
  display: block;
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
}



