
.today {
  position: relative;
  height: 100px;
  color: #fff;
}
.today .today-header {
  position: absolute;
  font-size: 14px;
  line-height: 28px;
  padding-left: 15px;
  top: -28px;
  width: 100%;
  box-sizing: border-box;
}
.today .today-header .mintmp {
  position: absolute;
  display: inline-block;
  right: 50px;
}
.today .today-header .maxtmp {
  position: absolute;
  display: inline-block;
  right: 15px;
}
.today .today-body {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  overflow: hidden;
  white-space: nowrap;
  padding: 0 5px;
}
.today .today-body ul {
  display: inline-block;
}
.today .today-body .hour {
  display: inline-block;
  margin: 0 10px;
}
.today .today-body .hour span {
  display: block;
  text-align: center;
  font-size: 12px;
  line-height: 30px;
}
.today .today-body .hour span.pop {
  font-size: 8px;
  line-height: 0px;
  padding: 5px 0 10px;
}
.today .today-body .hour span.icon {
  font-size: 25px;
  height: 25px;
}



